import { useState } from 'react';
import PropTypes from 'prop-types';
import { isValid } from 'date-fns';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import TableRow from '@mui/material/TableRow';
import Checkbox from '@mui/material/Checkbox';
import TableCell from '@mui/material/TableCell';
import IconButton from '@mui/material/IconButton';
import { TextField, Typography } from '@mui/material';
import ListItemText from '@mui/material/ListItemText';
import InputAdornment from '@mui/material/InputAdornment';

import { paths } from 'src/routes/paths';
import { useRouter } from 'src/routes/hooks';

import { useBoolean } from 'src/hooks/use-boolean';

import { fDate } from 'src/utils/format-time';

import { useAclGuard } from 'src/auth/guard/acl-guard';
import { useLocales, useTranslate } from 'src/locales';
import useUSTypeGuard from 'src/auth/guard/USType-guard';

import Label from 'src/components/label';
import Iconify from 'src/components/iconify';
import { ConfirmDialog } from 'src/components/custom-dialog';
import CustomPopover, { usePopover } from 'src/components/custom-popover';

import BookManually from './book-appointment-manually';
import UploadAnalysis from '../patients/upload-analysis';

// ----------------------------------------------------------------------

export default function AppointmentsTableRow({
  row,
  selected,
  onSelectRow,
  refetch,
  onViewRow,
  onDelayRow,
  onCancelRow,
  onUnCancelRow,
  onBookAppoint,
}) {
  const {
    _id,
    appoint_number,
    unit_service,
    work_group,
    medicalAnalysis,
    note,
    appointment_type,
    patient,
    unit_service_patient,
    start_time,
    status,
    invoiced,
    created_at,
    user_creation,
    ip_address_user_creation,
    updated_at,
    user_modification,
    ip_address_user_modification,
    modifications_nums,
  } = row;

  const router = useRouter();

  const { t } = useTranslate();

  const checkAcl = useAclGuard();
  const { isMedLab } = useUSTypeGuard();

  const { currentLang } = useLocales();
  const curLangAr = currentLang.value === 'ar';

  const popover = usePopover();
  const DDL = usePopover();
  const confirmDelayOne = useBoolean();
  const Book = useBoolean();
  const uploadAnalysis = useBoolean();

  const [minToDelay, setMinToDelay] = useState(0);

  let patientName;
  if (patient) {
    patientName = curLangAr ? patient?.name_arabic : patient?.name_english;
  } else if (unit_service_patient) {
    patientName = curLangAr
      ? unit_service_patient?.name_arabic
      : unit_service_patient?.name_english;
  }

  return (
    <>
      <TableRow hover selected={selected}>
        <TableCell padding="checkbox">
          <Checkbox checked={selected} onClick={onSelectRow} />
        </TableCell>

        <TableCell align="center">
          <ListItemText
            primary={
              isValid(new Date(start_time)) &&
              new Date(start_time).toLocaleTimeString(t('en-US'), {
                timeZone: unit_service?.country?.time_zone || 'Asia/Amman',
                hour: '2-digit',
                minute: '2-digit',
              })
            }
            secondary={
              isValid(new Date(start_time)) &&
              new Date(start_time).toLocaleDateString(t('en-US'), {
                timeZone: unit_service?.country?.time_zone || 'Asia/Amman',
              })
            }
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              mt: 0.5,
              component: 'span',
              typography: 'caption',
            }}
          />
        </TableCell>
        <TableCell align="center">{appoint_number}</TableCell>
        <TableCell align="center">
          {curLangAr ? appointment_type?.name_arabic : appointment_type?.name_english}
        </TableCell>
        <TableCell
          align="center"
          sx={{
            cursor: 'pointer',
            color: '#3F54EB',
          }}
          onClick={() => {
            if (unit_service_patient)
              router.push(paths.employee.patients.info(unit_service_patient?._id));
          }}
        >
          {patientName}
        </TableCell>
        <TableCell align="center">{note}</TableCell>
        {isMedLab && (
          <TableCell align="center">
            <Iconify
              icon={medicalAnalysis ? 'eva:checkmark-fill' : 'mingcute:close-line'}
              width={16}
            />
          </TableCell>
        )}
        <TableCell align="center">
          {curLangAr ? work_group?.name_arabic : work_group?.name_english}
        </TableCell>
        {/* <TableCell  align="center">
          {curLangAr ? work_shift?.name_arabic : work_shift?.name_english}
        </TableCell> */}

        <TableCell align="center">
          <Label
            variant="soft"
            color={
              (status === 'processing' && 'info') ||
              (status === 'arrived' && 'success') ||
              (status === 'late' && 'warning') ||
              (status === 'booked' && 'info') ||
              (status === 'finished' && 'success') ||
              (status === 'not arrived' && 'error') ||
              (status === 'canceled' && 'warning') ||
              (status === 'available' && 'secondary') ||
              (status === 'not booked' && 'secondary') ||
              'default'
            }
          >
            {t(status)}
          </Label>
        </TableCell>

        <TableCell align="right" sx={{ px: 1 }}>
          {isMedLab && !medicalAnalysis && (
            <IconButton lang="ar" onClick={uploadAnalysis.onTrue}>
              <Iconify icon="octicon:upload-16" />
            </IconButton>
          )}
          <IconButton color={popover.open ? 'inherit' : 'default'} onClick={popover.onOpen}>
            <Iconify icon="eva:more-vertical-fill" />
          </IconButton>
        </TableCell>
      </TableRow>

      <BookManually refetch={refetch} appointment={row} open={Book.value} onClose={Book.onFalse} />

      <CustomPopover
        open={popover.open}
        onClose={popover.onClose}
        arrow="right-top"
        sx={{ width: 155 }}
      >
        {checkAcl({ category: 'unit_service', subcategory: 'appointments', acl: 'update' }) && (
          <MenuItem
            lang="ar"
            onClick={() => {
              router.push(paths.unitservice.appointments.edit(_id));
              popover.onClose();
            }}
          >
            <Iconify icon="fluent:edit-32-filled" />
            {t('edit')}
          </MenuItem>
        )}
        {['processing', 'finished'].includes(status) &&
          !invoiced &&
          checkAcl({ category: 'unit_service', subcategory: 'accounting', acl: 'create' }) && (
            <MenuItem
              lang="ar"
              onClick={() => {
                router.push(
                  `${paths.unitservice.accounting.economicmovements.add}?appointment=${_id}`
                );
                popover.onClose();
              }}
              sx={{ color: 'info.main' }}
            >
              <Iconify icon="hugeicons:invoice" />
              {t('make an invoice')}
            </MenuItem>
          )}
        {status === 'available' &&
          checkAcl({ category: 'unit_service', subcategory: 'appointments', acl: 'update' }) && (
            <MenuItem
              lang="ar"
              sx={{ color: 'success.main' }}
              onClick={() => {
                onBookAppoint();
                popover.onClose();
              }}
            >
              <Iconify icon="mdi:register" />
              {t('book manually')}
            </MenuItem>
          )}
        {status === 'available' &&
          checkAcl({ category: 'unit_service', subcategory: 'appointments', acl: 'delete' }) && (
            <MenuItem
              lang="ar"
              onClick={() => {
                onCancelRow();
                popover.onClose();
              }}
              sx={{ color: 'error.main' }}
            >
              <Iconify icon="mdi:bell-cancel" />
              {t('cancel')}
            </MenuItem>
          )}
        {status === 'canceled' &&
          checkAcl({ category: 'unit_service', subcategory: 'appointments', acl: 'update' }) && (
            <MenuItem
              lang="ar"
              onClick={() => {
                onUnCancelRow();
                popover.onClose();
              }}
              sx={{ color: 'success.main' }}
            >
              <Iconify icon="material-symbols-light:notifications-active-rounded" />
              {t('uncancel')}
            </MenuItem>
          )}
        {checkAcl({ category: 'unit_service', subcategory: 'appointments', acl: 'update' }) &&
          !['finished', 'canceled', 'not booked'].includes(status) && (
            <MenuItem lang="ar" onClick={confirmDelayOne.onTrue}>
              <Iconify icon="mdi:timer-sync" />
              {t('delay')}
            </MenuItem>
          )}
        <MenuItem lang="ar" onClick={DDL.onOpen}>
          <Iconify icon="carbon:data-quality-definition" />
          {t('DDL')}
        </MenuItem>
        {isMedLab && !medicalAnalysis && (
          <MenuItem lang="ar" onClick={uploadAnalysis.onTrue}>
            <Iconify icon="octicon:upload-16" />
            {t('upload analysis')}
          </MenuItem>
        )}
      </CustomPopover>

      <CustomPopover
        open={DDL.open}
        onClose={DDL.onClose}
        arrow="right-top"
        sx={{
          padding: 2,
          fontSize: '14px',
        }}
      >
        <Box sx={{ fontWeight: 600 }}>{t('creation time')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={fDate(created_at, 'dd MMMMMMMM yyyy')}
            secondary={fDate(created_at, 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('created by')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_creation?.email}</Box>

        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('created by IP')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{ip_address_user_creation}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editing time')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>
          <ListItemText
            primary={fDate(updated_at, 'dd MMMMMMMM yyyy')}
            secondary={fDate(updated_at, 'p')}
            primaryTypographyProps={{ typography: 'body2', noWrap: true }}
            secondaryTypographyProps={{
              component: 'span',
              typography: 'caption',
            }}
          />
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editor')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray' }}>{user_modification?.email}</Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>{t('editor IP')}:</Box>
        <Box sx={{ pb: 1, borderBottom: '1px solid gray', fontWeight: '400' }}>
          {ip_address_user_modification}
        </Box>
        <Box sx={{ pt: 1, fontWeight: 600 }}>
          {t('modifications no')}: {modifications_nums}
        </Box>
      </CustomPopover>
      <ConfirmDialog
        open={confirmDelayOne.value}
        onClose={confirmDelayOne.onFalse}
        title={t('delay')}
        content={
          <>
            {t('How many minutes do you want to delay?')}
            <TextField
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Box sx={{ fontSize: '0.8rem' }}>{t('min')}</Box>
                  </InputAdornment>
                ),
              }}
              type="number"
              sx={{ p: 2, width: '100%' }}
              size="small"
              onChange={(e) => setMinToDelay(e.target.value)}
              helperText={t('knowing that you can type a negative value to make it earlier')}
            />
            {minToDelay !== 0 && (
              <Typography variant="body2" sx={{ textAlign: 'center', color: 'error.main' }}>
                {t('from')}&nbsp;
                {new Date(start_time).toLocaleTimeString(t('en-US'), {
                  timeZone: unit_service?.country?.time_zone || 'Asia/Amman',
                  hour: '2-digit',
                  minute: '2-digit',
                })}
                {/* {'     >>     '} */}
                &nbsp;{t('to')}&nbsp;
                {new Date(
                  new Date(start_time).getTime() + minToDelay * 60 * 1000
                ).toLocaleTimeString(t('en-US'), {
                  timeZone: unit_service?.country?.time_zone || 'Asia/Amman',
                  hour: '2-digit',
                  minute: '2-digit',
                })}
              </Typography>
            )}
          </>
        }
        action={
          <Button
            variant="contained"
            color="info"
            onClick={() => {
              confirmDelayOne.onFalse();
              onDelayRow(row, minToDelay);
            }}
          >
            {t('delay')}{' '}
          </Button>
        }
      />
      <UploadAnalysis
        open={uploadAnalysis.value}
        onClose={uploadAnalysis.onFalse}
        analysisData={{ patient: patient?._id || patient, appointment: _id }}
        refetch={refetch}
      />
    </>
  );
}

AppointmentsTableRow.propTypes = {
  onBookAppoint: PropTypes.func,
  onCancelRow: PropTypes.func,
  onUnCancelRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onViewRow: PropTypes.func,
  onDelayRow: PropTypes.func,
  refetch: PropTypes.func,
  row: PropTypes.object,
  selected: PropTypes.bool,
};
